import { Popup } from "antd-mobile";

const SysPopup = ({ title, show, setShow, children, height }) => {
    return (
        <Popup
            visible={show}
            onMaskClick={() => {
                setShow(false)
            }}
            onClose={() => {
                setShow(false)
            }}
            bodyStyle={{
                height: height ? height : 'calc(100vh - 50px)',
                borderTopLeftRadius: '20px',
                borderTopRightRadius: '20px',
            }}
        >
            <div className="sys-popup">
                <div className="sys-popup-head">
                    <div className="sys-popup-head-title">{title}</div>
                </div>
                <div className="sys-popup-content">
                    {children}
                </div>
            </div>
        </Popup>
    );
}
export default SysPopup;