import "../../../../../assets/css/xdata/app/habit-data-app.css";
import { useEffect, useState } from "react";
import { handleUndefined } from "../../../../../assets/js/stringUtil";
import { Util } from "../../../../../assets/js/util";
import { sortArray } from "../../../../../assets/js/sort";
import SysPopup from "../../../../../component/SysPopup";
import { CloseOutline } from "antd-mobile-icons";

function HabitDataApp(props) {
    const todoOption = handleUndefined(props.todoOption, { title: '', code: '' });
    const [todoList, setTodoList] = useState([]);
    const [doneList, setDoneList] = useState([]);
    const [allDoneDays, setAllDoneDays] = useState(0);
    const [todoOptionList, setTodoOptionList] = useState([]);
    const [showDoneList, setShowDoneList] = useState(false);
    useEffect(function () {
        let todoOptionListTemp = [
            { code: "sleep1", title: "", time: "06:30", type: "" },
            { code: "water1", title: "", time: "06:35", type: "" },
            { code: "water2", title: "", time: "10:00", type: "" },
            { code: "water3", title: "", time: "12:30", type: "" },
            { code: "eat1", title: "", time: "12:30", type: "" },
            { code: "sport1", title: "", time: "12:40", type: "" },
            { code: "water4", title: "", time: "14:00", type: "" },
            { code: "water5", title: "", time: "15:00", type: "" },
            { code: "water6", title: "", time: "16:00", type: "" },
            { code: "sport2", title: "30", time: "21:00", type: "" },
            { code: "eat2", title: "", time: "22:00", type: "" },
            { code: "sleep2", title: "", time: "22:30", type: "" },
            { code: "aStudy", title: "A", time: "08:00", type: "" },
            { code: "cStudy", title: "", time: "19:00", type: "" },
            { code: "bStudy", title: "B", time: "20:00", type: "" },
        ];
        setTodoOptionList(todoOptionListTemp);
        let nowDoneList = getNowDoneList();
        let todoListTemp = [];
        let doneListTemp = [];
        todoOptionListTemp.forEach((todo) => {
            if (nowDoneList.toString().indexOf(todo.code) === -1) {
                todoListTemp.push(todo);
            } else {
                doneListTemp.push(todo);
            }
        });
        todoListTemp = sortArray(todoListTemp, "time", true);
        doneListTemp = sortArray(doneListTemp, "time", true);
        setTodoList(todoListTemp);
        setDoneList(doneListTemp);
        getAllDoneDays(todoOptionListTemp);
    }, []);// eslint-disable-line

    function getAllDoneDays(todoOptionListTemp) {
        let allDoneDaysTemp = 0;
        let allDoneMap = getAllDoneMap();
        if (!todoOptionListTemp) {
            todoOptionListTemp = todoOptionList;
        }
        for (let key of Object.keys(allDoneMap)) {
            if (allDoneMap[key].length === todoOptionListTemp.length) {
                allDoneDaysTemp += 1;
            }
        }
        setAllDoneDays(allDoneDaysTemp);
    }

    function getNowDoneList() {
        const todoOptionCode = todoOption.code;
        const nowDate = Util.getNowDate();
        let doneDateMapStr = localStorage.getItem(todoOptionCode + "-doneDateMap");
        if (!doneDateMapStr) {
            doneDateMapStr = "{}";
        }
        let doneDateMap = JSON.parse(doneDateMapStr);
        let nowDoneList = doneDateMap[nowDate];
        if (!nowDoneList) {
            nowDoneList = [];
        }
        return nowDoneList;
    }

    function getAllDoneMap() {
        const todoOptionCode = todoOption.code;
        let doneDateMapStr = localStorage.getItem(todoOptionCode + "-doneDateMap");
        if (!doneDateMapStr) {
            doneDateMapStr = "{}";
        }
        let doneDateMap = JSON.parse(doneDateMapStr);
        return doneDateMap;
    }

    function setNowDoneList(doneList) {
        const todoOptionCode = todoOption.code;
        const nowDate = Util.getNowDate();
        let doneDateMapStr = localStorage.getItem(todoOptionCode + "-doneDateMap");
        if (!doneDateMapStr) {
            doneDateMapStr = "{}";
        }
        let doneDateMap = JSON.parse(doneDateMapStr);
        doneDateMap[nowDate] = doneList;
        localStorage.setItem(todoOptionCode + "-doneDateMap", JSON.stringify(doneDateMap));
    }

    function doneTodo(done) {
        let todoListTemp = todoList;
        todoListTemp = todoListTemp.filter(todo => todo.code !== done.code);
        todoListTemp = sortArray(todoListTemp, "time", true);
        setTodoList(todoListTemp);

        let doneListTemp = doneList;
        doneList.push(done);
        doneListTemp = sortArray(doneListTemp, "time", true);
        setDoneList(doneListTemp);
        const doneDateListTemp = getNowDoneList();
        doneDateListTemp.push(done.code);
        setNowDoneList(doneDateListTemp);
        getAllDoneDays();
    }

    function unDoneTodo(unDone) {
        let todoListTemp = todoList;
        todoListTemp.push(unDone);
        todoListTemp = sortArray(todoListTemp, "time", true);
        setTodoList(todoListTemp);

        let doneListTemp = doneList;
        doneListTemp = doneListTemp.filter(done => done.code !== unDone.code);
        doneListTemp = sortArray(doneListTemp, "time", true);
        setDoneList(doneListTemp);
        let doneDateListTemp = getNowDoneList();
        doneDateListTemp = doneDateListTemp.filter(dateDoneCode => dateDoneCode !== unDone.code);
        setNowDoneList(doneDateListTemp);
        getAllDoneDays();
    }

    return (
        <div className="app-card">
            <div className="app-card-head">
                <div className="app-card-head-title">{todoOption.title}</div>
                <div className="app-card-head-other habit-head-stat">
                    <span onClick={() => {
                        setShowDoneList(true);
                    }}>
                        {todoOptionList.length - doneList.length}
                    </span>
                    &nbsp;&nbsp;
                    <span onClick={() => {
                        setShowDoneList(true);
                    }}>
                        {doneList.length}
                    </span>
                    &nbsp;&nbsp;
                    <span>{allDoneDays} </span>
                    <SysPopup title={': ' + doneList.length + '/' + todoOptionList.length} show={showDoneList} setShow={setShowDoneList}>
                        <div className="done-list">
                            <div className="sys-empty flex-center"
                                style={{ display: doneList.length === 0 ? undefined : 'none' }}>
                                <div className="sys-empty-icon"></div>
                                <div className="sys-empty-text"></div>
                            </div>
                            {
                                doneList.map((item, index) => {
                                    return (
                                        <div className="done-list-item" key={index}>
                                            <div className="done-list-item-title">
                                                {item.type}&nbsp;&nbsp;{item.time}&nbsp;{item.title}
                                            </div>
                                            <div className="done-list-item-operate flex-center">
                                                <div className="done-list-btn flex-center"
                                                    onClick={() => unDoneTodo(item)}>
                                                </div>
                                            </div>
                                        </div>
                                    );
                                })
                            }
                        </div>
                        <div className="sys-popup-bottom">
                            <div className="sys-icon-btn big ignore" onClick={() => setShowDoneList(false)}>
                                <CloseOutline />
                            </div>
                        </div>
                    </SysPopup>
                </div>
                <div className="habit-head-right"></div>
            </div>
            <div className="app-card-main habit-data-app-main">
                <div className="sys-empty flex-center"
                    style={{ display: todoList.length === 0 ? undefined : 'none' }}>
                    <div className="sys-empty-icon"> </div>
                    <div className="sys-empty-text"></div>
                </div>
                {
                    todoList.map((item, index) => {
                        return (
                            <div className="habit-item" onClick={() => doneTodo(item)} key={index}>
                                <div className="habit-item-icon">{item.type}</div>
                                <div className="habit-item-time">{item.time}</div>
                                <div className="habit-item-title">{item.title}</div>
                                <div className="habit-item-stat"></div>
                            </div>
                        );
                    })
                }
            </div>
        </div>
    );
}

export default HabitDataApp;