import "../../assets/css/dataEnv/data-env.css";
import { useEffect, useState } from "react";
import { Util } from "../../assets/js/util";

function DataEnv() {
    const [evnHelloText, setEvnHelloText] = useState("-");
    const [nowDay, setNowDay] = useState("-");
    useEffect(function () {
        setEvnHelloText(Util.getTimeHello());
        setNowDay(Util.getNowDay());
    }, [])

    return (
        <div className="system-env flex-center">
            <div className="system-env-left">
                <div className="env-date">
                    {(new Date().getMonth()+ 1)+ '' + new Date().getDate() + ',' + nowDay}
                </div>
            </div>
            <div className="system-env-right">
                <div className="env-hello">{evnHelloText}</div>
            </div>
        </div>
    );
}

export default DataEnv;