import "../../../../../assets/css/xdata/app/todo-line.css";
import {
    AddOutline,
    CheckCircleFill,
    CheckOutline,
    CloseOutline,
    ExclamationTriangleOutline
} from "antd-mobile-icons";
import { useEffect, useState } from "react";
import { Toast } from "antd-mobile";
import { sortArray } from "../../../../../assets/js/sort";
import { Util } from "../../../../../assets/js/util";
import SysPopup from "../../../../../component/SysPopup";
import { SysInput } from "../../../../../component/SysInput";

function TodoLine() {
    const [todoLineList, setTodoLineList] = useState([]);
    const [showSaveTodo, setShowSaveTodo] = useState(false);
    const [saveTodoForm, setSaveTodoForm] = useState({});
    const dataKey = 'todoLineList-' + Util.getNowDate();
    useEffect(() => {
        setTodoLineList(getTodoLineList());
    }, []);// eslint-disable-line

    function getTodoLineList() {
        const todoLineListJson = localStorage.getItem(dataKey);
        let todoLineListTemp = [];
        if (todoLineListJson && todoLineListJson !== '') {
            todoLineListTemp = JSON.parse(todoLineListJson);
        }
        todoLineListTemp = sortArray(todoLineListTemp, 'time');
        return todoLineListTemp;
    }

    function addTodoLineList(item) {
        item.id = new Date().getTime();
        let todoLineListTemp = getTodoLineList();
        todoLineListTemp.push(item);
        todoLineListTemp = sortArray(todoLineListTemp, 'time');
        setTodoLineList(todoLineListTemp);
        const todoLineListJson = JSON.stringify(todoLineListTemp);
        localStorage.setItem(dataKey, todoLineListJson);
    }

    function saveTodo() {
        if (!saveTodoForm.title) {
            Toast.show({
                content: '',
                icon: <ExclamationTriangleOutline />,
            });
            return;
        }
        if (!saveTodoForm.time) {
            Toast.show({
                content: '',
                icon: <ExclamationTriangleOutline />,
            });
            return;
        }
        const todoItem = { ...saveTodoForm };
        addTodoLineList(todoItem);
        setShowSaveTodo(false);
    }

    function setDone(done) {
        let todoLineListTemp = todoLineList;
        todoLineListTemp = todoLineListTemp.filter(item => item.id !== done.id);
        if (done.ok === 1) {
            done.ok = 0;
        } else {
            done.ok = 1;
        }
        todoLineListTemp.push(done);
        todoLineListTemp = sortArray(todoLineListTemp, 'time');
        setTodoLineList(todoLineListTemp);
        localStorage.setItem(dataKey, JSON.stringify(todoLineListTemp));
    }
    function delTodo(del) {
        if (window.confirm('' + del.title + '?')) {
            let todoLineListTemp = todoLineList;
            todoLineListTemp = todoLineListTemp.filter(item => item.id !== del.id);
            todoLineListTemp = sortArray(todoLineListTemp, 'time');
            setTodoLineList(todoLineListTemp);
            localStorage.setItem(dataKey, JSON.stringify(todoLineListTemp));
        }
    }
    return (
        <div className="app-card">
            <div className="app-card-head">
                <div className="app-card-head-title">
                    
                </div>
                <div className="app-card-head-other">
                    <div className="sys-icon-btn" onClick={() => {
                        saveTodoForm.title = '';
                        saveTodoForm.time = '';
                        setSaveTodoForm(saveTodoForm);
                        setShowSaveTodo(true);
                    }}>
                        <AddOutline />
                    </div>
                </div>
            </div>
            <div className="app-card-main todo-line-main">
                <div className="sys-empty flex-center"
                    style={{ display: todoLineList.length === 0 ? undefined : 'none' }}>
                    <div className="sys-empty-icon"> </div>
                    <div className="sys-empty-text"></div>
                </div>
                {
                    todoLineList.map((item, index) => {
                        return (
                            <div className="todo-line-item" key={index} onDoubleClick={() => {
                                delTodo(item);
                            }}>
                                <div className="todo-line-head">
                                    <div className={item.ok === 1 ? "todo-line-icon-active" : "todo-line-icon"} onClick={() => setDone(item)}>
                                        <CheckCircleFill />
                                    </div>
                                    <div className={item.ok === 1 ? "todo-line-active" : "todo-line"}
                                        style={{ display: index !== todoLineList.length - 1 ? undefined : 'none' }}></div>
                                </div>
                                <div className="todo-line-time">{item.time}</div>
                                <div className="todo-line-title">{item.title}</div>
                            </div>
                        );
                    })
                }
                <SysPopup title='' show={showSaveTodo} setShow={setShowSaveTodo}>
                    <SysInput title='' type='text' onChange={(e) => {
                        saveTodoForm.title = e.target.value;
                        setSaveTodoForm(saveTodoForm);
                    }} />
                    <SysInput title='' type='time' onChange={(e) => {
                        saveTodoForm.time = e.target.value;
                        setSaveTodoForm(saveTodoForm);
                    }} />
                    <div className="sys-popup-bottom">
                        <div className="sys-icon-btn big" onClick={() => saveTodo()}>
                            <CheckOutline />
                        </div>
                        <div className="sys-icon-btn big ignore" onClick={() => setShowSaveTodo(false)}>
                            <CloseOutline />
                        </div>
                    </div>
                </SysPopup>
            </div>
        </div>
    );
}

export default TodoLine;
