import { handleUndefined } from "../assets/js/stringUtil";

export function SysInput(props) {
    const title = handleUndefined(props.title, '');
    const type = handleUndefined(props.type, '');
    const options = handleUndefined(props.options, []);
    const onChange = handleUndefined(props.onChange, undefined);
    return (
        <div>
            <div className="sys-input">
                <div className="sys-input-title">{title}</div>
                <div className="sys-input-content">
                    <input style={{ display: props.type !== 'select' ? undefined : 'none' }} type={type} onChange={(e) => {
                        onChange(e)
                    }} />
                    <select style={{ display: props.type === 'select' ? undefined : 'none' }} onChange={(e) => {
                        onChange(e)
                    }}>
                        {
                            options.map((optionItem, index) => {
                                return (
                                    <option key={index} value={optionItem.value}>{optionItem.desc}</option>
                                );
                            })
                        }
                    </select>
                </div>
            </div>
        </div>
    );
}